
/* eslint-disable */
const banner = (() => {
	const bannerInit = () => {
		const CLASSES = {};

		if ($(".c-banner").length) {
			$(".banner-1 .form").submit(function(e){
                e.preventDefault();
                $(".banner-1").addClass("hidden");
                $(".banner-2").removeClass("hidden");
                return false;
            })
		}
	};

	const init = () => {
		bannerInit();
	};

	return {
		init
	};
})();

export default banner;
