/* eslint-disable */
const navbarDropdown = (() => {
	const toggleInit = () => {
		const CLASSES = {};
		$(".menu-trigger").on("click", function(e) {
			e.preventDefault();

			var _this = $(this);
			var _target = $(".menu-nav");
			var _overlay = $(".c-overlay");
			_target.addClass("active");
			$("body").css("overflow","hidden");
			_overlay.fadeIn(500).addClass(" overlay-navbar active");
			
		});
		function removeNavbar(){
			$(".menu-nav").removeClass("active");
			$(".overlay-navbar").fadeOut(500,function(){
				$(this).removeClass("active overlay-navbar");
				$("body").css("overflow","visible");
			})
			
		}
		$(document).on("click",".overlay-navbar",removeNavbar);
		$(".menu-close").click(removeNavbar);
		
	};

	const init = () => {
		toggleInit();
	};

	return {
		init
	};
})();

export default navbarDropdown;
