/* eslint-disable */
const dropdown = (() => {
    
    const clickListener = () =>{

        function toggleDropdown(){
            var _target = $(".dropdown-list");
            if(_target.is(":visible")){
                _target.removeClass("active");
                $(".c-overlay").fadeOut(500).removeClass(" overlay-dropdown active");
            }else{
                _target.addClass("active");
                $(".c-overlay").fadeIn(500).addClass(" overlay-dropdown active");
            }
        }
        $(document).on("click",".dropdown",toggleDropdown);

        $(document).on("click",".overlay-dropdown",toggleDropdown);
    }

	const init = () => {
		clickListener();
	};

	return {
		init
	};
})();

export default dropdown;
