// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const reviewsCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".reviews-carousel").length) {
			const slider = $(".reviews-carousel");

			slider.slick({
				rows: 0,
				dots: false,
				arrows: false,
				autoplay: true,
				autoplaySpeed: 4000,
				infinite:true,
				// appendDots: $(".js-banner-carousel-pagination"),
				cssEase: "cubic-bezier(0.645, 0.045, 0.355, 1)",
                speed: 500,
                slidesToShow: 3,
                slidesToScroll: 3,
                responsive: [
                    {
                      breakpoint: 1024,
                      settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                      
                      }
                    },
                    {
                      breakpoint: 600,
                      settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2
                      }
                    },
                    {
                      breakpoint: 480,
                      settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                      }
                    }
             
                  ]
			
			});
		}
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default reviewsCarousel;
