// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const bannerCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".js-banner-carousel").length) {
			const slider = $(".js-banner-carousel");

			slider.slick({
				rows: 0,
				dots: false,
				arrows: false,
				autoplay: true,
				autoplaySpeed: 5000,
				pauseOnHover: false,
				cssEase: "cubic-bezier(0.645, 0.045, 0.355, 1)",
				speed: 500
			});
		}
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default bannerCarousel;
