/* eslint-disable */
const videoPopup = (() => {
	const modalInit = () => {
		const CLASSES = {};

		if ($('[data-video-url]').length) {
			$(document).on('click', '[data-video-url]', function(e) {
				e.preventDefault();
				var url = $(this).data('video-url');
				var youTubeVideoId = get_youtubeId(url);

				var video =
					"<iframe width='100%' class='youtube-player' src='https://www.youtube.com/embed/" +
					youTubeVideoId +
					"?rel=0&amp;autoplay=1' frameborder='0' allowfullscreen></iframe>";
				initVideo(video);
			});

			$(document).on('click', '.c-modal-video__close', function() {
				closePopup();
			});

			$(document).on('click', '.c-modal-video__overlay', function() {
				closePopup();
			});

			function closePopup() {
				stop_youtubeVideo();
				$('.c-modal-video').remove();
			}

			function initVideo(video) {
				// creating overlay before video pops up
				let overlay = "<div class='c-modal-video'>";
				overlay += "<div class='c-modal-video__close'></div>";
				overlay += "<div class='c-modal-video__overlay'></div>";
				overlay +=
					"<div class='c-modal-video__content'><div class='video'>" +
					video +
					'</div></div>';
				overlay += '</div>';
				$('body').append(overlay);
			}

			function get_youtubeId(url) {
				var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
				var match = url.match(regExp);
				if (match && match[2].length == 11) {
					return match[2];
				} else {
					//error
				}
			}

			function stop_youtubeVideo() {
				$('.youtube-player').each(function() {
					this.contentWindow.postMessage(
						'{"event":"command","func":"stopVideo","args":""}',
						'*'
					);
				});
			}
		}
	};
	const setVideoImage = () =>{
		function get_youtubeId(url) {
			var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
			var match = url.match(regExp);
			if (match && match[2].length == 11) {
				return match[2];
			} else {
				//error
			}
		}
		if ($('[data-video-url]').length){
			$('[data-video-url]').each(function(){
				let videoUrl = $(this).data("video-url");
				let videoId = get_youtubeId(videoUrl);
				var imageUrl = "http://i.ytimg.com/vi/"+videoId;

				var image= "<img src='"+imageUrl+"/mqdefault.jpg' alt='' class='w-full lg:hidden'/> <img src='"+imageUrl+"/mqdefault.jpg' alt='' class='hidden w-full lg:block'/>";
				$(this).html(image);
			});
		}
	}
	const init = () => {
		modalInit();
		setVideoImage();
	};

	return {
		init
	};
})();

export default videoPopup;
