// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const articleCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".othersizes .product-carousel").length) {
			const slider = $(".othersizes .product-carousel");

			slider.slick({
				rows: 0,
				dots: true,
				arrows: false,
				// prevArrow: $('.js-banner-carousel-prev'),
				// nextArrow: $('.js-banner-carousel-next'),
				// appendDots: $(".js-banner-carousel-pagination"),
				cssEase: "cubic-bezier(0.645, 0.045, 0.355, 1)",
				speed: 500,
				centerMode: false,
				slidesToShow: 4,
				slidesToScroll: 1,
				responsive: [
					{
						breakpoint: 1199,
						settings: {
							slidesToShow: 4,
							centerMode: false
						}
					},
					{
						breakpoint: 980,
						settings: {
							slidesToShow: 3,
							centerMode: true
						}
					},

					{
						breakpoint: 660,
						settings: {
							slidesToShow: 2,
							centerMode: true
						}
					},
					{
						breakpoint: 480,
						settings: {
							slidesToShow: 1,
							centerMode: true
						}
					}
				]
			});
		}
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default articleCarousel;
