// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const testimonialCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".testimonials__slider").length) {
			const slider = $(".testimonials__slider");

			slider.slick({
				rows: 0,
				dots: false,
				arrows: true,
				// autoplay: true,
				// autoplaySpeed: 5000,
				prevArrow: $(".testimonial-arrow__left"),
				nextArrow:$(".testimonial-arrow__right"),
				// appendDots: $(".js-banner-carousel-pagination"),
				cssEase: "cubic-bezier(0.645, 0.045, 0.355, 1)",
				speed: 500,
			
			});
		}
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default testimonialCarousel;
